.imoveis-list .text {
  padding: 30px;
}
.imoveis-list .category span {
  padding: 4px 15px;
  border-radius: 2px;
  font-weight: 600;
  font-size: 13px;
  display: inline-block;
}

.imoveis-list .category i {
  font-size: 20px;
  color: #bec7d8;
}
.imoveis-list p {
  color: #72809d;
}
.imoveis-list .button {
  border-top: 1px solid #e4e8f3;
  padding: 10px 30px;
}


.imoveis-grid{
    display:grid;
    grid-template-columns:320px 1fr;
    gap:24px;
    margin-top:20px}
    
    .imoveis-filter{position:sticky;top:90px}.imoveis-list{min-height:200px}

@media(max-width:900px)
{
    .imoveis-grid{grid-template-columns:1fr;margin:2.5%}
.imoveis-filter{position:relative;top:0}
}